.buttonGroup {
  height: 100%;
  width: 90%;
  align-items: center;
  padding-top: 10px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin-left: auto;

  .btnBox {
    // display: flex;
    // justify-content: flex-end;
    display: flex;
    justify-content: center;

    .button {
      width: 3rem;
      height: 3.5rem;
      border-radius: 5px;
      box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.7);
      padding: 0;
      margin: 0;
    }
  }
}

.buttonGroupLandscape {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-auto-flow: column; //直向排列
  width: 100%;
  height: 80%;
  margin-top: 3rem;
  padding-left: 0.3rem;

  .btnBoxLandscape {
    height: 3rem;
    width: 3rem;
    .buttonLandscape {
      width: 100%;
      height: 100%;
      border-radius: 5px;
      box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.7);
      padding: 0;
    }
  }
}

.buttonGroupIsOrientationVertical {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  padding: 10px;

  .buttonBoxIsOrientationVertical {
  }

  .buttonIsOrientationVertical {
    transform: rotate(90deg);
    width: 90%;
    height: 90%;
    border-radius: 5px;
    box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.7);
    padding: 0;
  }
}
